.accordion li {
  list-style: none;
  padding: 0px 0px 20px;
}

.accordion li a {
  position: relative;
  width: 100%;
  display: block;
  cursor: pointer;
  font-weight: 600;
  font-size: 20px;
  color: #232323 !important;
  background-color: #f4f0ed;
  border: 2px solid #ffffff;
  padding: 16px 20px 16px 25px;
  z-index: 1;
}

.accordion li a span {
  font-size: 20px;
  color: var(--secondary-color);
  padding: 0 15px 0 0px;
  position: relative;
  z-index: 1;
}

.accordion li a span:before {
  position: absolute;
  content: "";
  top: -8px;
  right: -765px;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.30196078431372547);
  border: 2px solid #ffffff;
}

.accordion li p {
  display: none;
  padding: 24px 15px 10px 25px;
  margin: 0;
  background: #f4f0ed;
  overflow: hidden;
  font-family: "DM Sans";
}

.accordion a:before {
  width: 3px;
  height: 15px;
  background: #97aa85;
  position: absolute;
  right: 41px;
  content: " ";
  top: 22px;
  transform: rotate(0deg);
  transition: all 0.5s ease-in-out;
}

.accordion a:after {
  width: 15px;
  height: 3px;
  background: #97aa85;
  position: absolute;
  right: 35px;
  content: " ";
  top: 28px;
  transition: all 0.5s ease-in-out;
  z-index: 1;
}

/* Active */
.accordion a.active:after {
  transform: rotate(0deg);
  -webkit-transition: all 0.5s ease-in-out;
  background: #fff;
}

.accordion a.active:before {
  display: none;
}

.accordion a.active {
  background: #97aa85;
  color: #fff !important;
  border: 2px solid #97aa85;
}

.accordion li a.active span {
  color: #fff;
}

