@-webkit-keyframes animate {
  50% {
    -ms-transform: rotate(360deg) scale(0.8);
    -webkit-transform: rotate(360deg) scale(0.8);
    transform: rotate(360deg) scale(0.8);
  }
}

@keyframes animate {
  50% {
    -ms-transform: rotate(360deg) scale(0.8);
    -webkit-transform: rotate(360deg) scale(0.8);
    transform: rotate(360deg) scale(0.8);
  }
}
/* Add this at the end of your CSS file */
.navbar-hamburger {
  display: none;
  cursor: pointer;
  z-index: 1001;
}
.navbar-hamburger span {
  display: block;
  width: 28px;
  height: 3px;
  margin: 6px 0;
  background: #232323;
  transition: 0.4s;
}

/* Show hamburger on mobile */
@media (max-width: 991px) {
  .navbar-hamburger {
    display: block;
  }
  .lovebird_menu {
    display: none;
  }
  .lovebird_menu.active {
    display: block;
  }
}

